
import { Component, OnInit, ElementRef, NgZone, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { CommonService } from "../services/commonService";
import { UserService } from "../services/userService";
import { WorkOrderService } from "../services/workOrderService";
import { ActivityService } from "../services/activityService";
import { MatDatepickerModule, MatNativeDateModule, DateAdapter } from '@angular/material';
import { MapsAPILoader } from '@agm/core';
import { Observable } from "rxjs/Observable";
import { } from 'googlemaps';
import { DomSanitizer, SafeHtml } from "@angular/platform-browser";
import swal from 'sweetalert2';
import { FileUploader } from 'ng2-file-upload';
import { PasswordValidation } from '../forms/validationforms/password-validator.component';
import { DatePipe } from '@angular/common'

declare const $: any;

declare interface DataTable {
    wAsgHeaderRow: string[];
    wAsgFooterRow: string[];
    wAsgDataRows: any;
}
@Component({
    selector: 'app-wo-assignment',
    templateUrl: './wo-assignment.component.html',
    providers: [CommonService, WorkOrderService, UserService, ActivityService, DatePipe],
    styles: [`md-calendar {
      width: 300px;
  }`]

})


export class WoAssignmentComponent implements OnInit {
    public latitude: number;
    public longitude: number;
    public searchControl: FormControl;
    public zoom: number;
    public workOrderDetail = {};
    public workOrderAssignmentList: any;
    public workOrderDocsList: any;
    public autoUsers: any;
    public wrokOrdAddress: any;
    public assUserID: any;
    public uploadedFiles: any[] = [];
    public isActivity: boolean = false;
    public workOrdActTrackerList: any;
    public sendDateTime: any;
    public isSend: boolean;

    public workOrdAssPage = new FormGroup({
        userName: new FormControl(),
        description: new FormControl(),
        ratetype: new FormControl(),
        rate: new FormControl(),
        fromdate: new FormControl(),
        todate: new FormControl(),
    });

    @ViewChild("search") searchElementRef: ElementRef;

    constructor(
        private mapsAPILoader: MapsAPILoader,
        private ngZone: NgZone,
        private http: Http,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private commonService: CommonService,
        private workOrderService: WorkOrderService,
        private activityService: ActivityService,
        private userService: UserService,
        private _sanitizer: DomSanitizer,
        private formBuilder: FormBuilder,
        private dateAdapter: DateAdapter<Date>,
        public datepipe: DatePipe
    ) {
        dateAdapter.setLocale('en-GB');
    }


    public uploader: FileUploader = new FileUploader({ url: this.commonService.baseApiUrl + "AddWorkOrderDocuments/", itemAlias: 'file' });
    public hasBaseDropZoneOver: boolean = false;
    public hasAnotherDropZoneOver: boolean = false;


    public fileOverBase(e: any): void {
        this.hasBaseDropZoneOver = e;
    }

    public fileOverAnother(e: any): void {
        this.hasAnotherDropZoneOver = e;
    }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    showSwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'

        }).catch(swal.noop);
    }

    showConfirmSwal(text: string) {
        swal({
            title: 'Are you sure?',
            text: text,
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).catch(swal.noop);
    }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {

            this.getAutocompleteUser();

            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['workID'] == undefined) {
                    this.router.navigate(['jobs']);
                }
                else {
                    var workOrdID = params['workID'];
                    this.getWorkOrdersByWorkOrdID(workOrdID);

                    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
                    this.uploader.onBuildItemForm = (fileItem: any, form: any) => {
                        form.append('WorkOrderId', localStorage.getItem("WorkOrderId"));
                        form.append('OldWorkOrderId', localStorage.getItem("OldWorkOrderId"));
                        form.append('WorkOrderNo', localStorage.getItem("WorkOrderNo"));
                    };
                    this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
                        this.getWorkOrderDocuments(localStorage.getItem("WorkOrderId"));
                    }
                    this.uploader.onCompleteAll = () => {
                        this.uploader.clearQueue();
                    };
                }
            });

            $(document).ready(function () {
                $(".cntEmpty").removeClass("is-empty");
                $(".collapse").on('show.bs.collapse', function () {
                    $(this).parent().find(".glyphicon").removeClass("glyphicon-plus").addClass("glyphicon-minus");
                }).on('hide.bs.collapse', function () {
                    $(this).parent().find(".glyphicon").removeClass("glyphicon-minus").addClass("glyphicon-plus");
                    });

                $('.number-decimal-only').keypress(function (e) {
                    var keyCode = e.which ? e.which : e.keyCode;
                    if ((keyCode != 46 || $(this).val().indexOf('.') != -1) && 
                        (keyCode < 48 || keyCode > 57))
                        return false;

                    return true;
                });
            });

            this.workOrdAssPage = this.formBuilder.group({
                userName: ['', Validators.required],
                description: ['', Validators.required],
                ratetype: ['', Validators.required],
                rate: ['', Validators.required],
                fromdate: ['', Validators.required],
                todate: ['', Validators.required]
            }, {
                    validator: PasswordValidation.DateValidation // your validation method
                });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    getAutocompleteUser() {
        $('.loader').show();
        this.userService.GetAutocompleteUser(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                this.resetWorkOrderAssignment();
                if (userInfo != null) {
                    this.autoUsers = userInfo;
                    $('.loader').hide();
                }
            });
    }

    activityDocShow() {
        this.isActivity = true;
        this.getActivityTracker(localStorage.getItem("WorkOrderNo"));
    }

    activityDocHide() {
        this.isActivity = false;
    }

    sendEmailWorkOrderAssignment() {
        $('.loader').show();
        if (this.workOrderAssignmentList != null) {
            this.workOrderService.SendEmailWorkOrderAssignment(this.commonService.baseApiUrl, this.workOrderAssignmentList, this.wrokOrdAddress).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo != null) {
                        this.showSwal("Work Order send successfully!!");
                        this.getWorkOrderAssignment(localStorage.getItem("WorkOrderId"));
                        this.autoUsers = userInfo;
                        $('.loader').hide();
                    }
                });
        }
    }

    deleteWorkOrderAssignment(workAsgId: any) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.workOrderService.DeleteWorkOrderAssignment(this.commonService.baseApiUrl, workAsgId).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo == true) {
                        this.getWorkOrderAssignment(localStorage.getItem("WorkOrderId"));
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'User has been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                });
        }).catch(swal.noop);
    }

    getWorkOrderAssignment(workOrdId: any) {
        $('.loader').show();
        this.workOrderService.GetWorkOrderAssignment(this.commonService.baseApiUrl, workOrdId).subscribe(
            data => {
                var datawAsg = JSON.stringify(data);
                var wAsgParse = JSON.parse(datawAsg);
                var wAsgList = JSON.parse(wAsgParse["_body"]);
                this.workOrderAssignmentList = null;
                if (wAsgList != null && wAsgList.length > 0) {
                    this.workOrderAssignmentList = wAsgList;
                    if (wAsgList[0].SendDateTime != null) {
                        this.sendDateTime = wAsgList[0].SendDateTime;
                    }
                    else {
                        this.sendDateTime = null;
                    }
                    $('.loader').hide();
                    this.isSend = false;
                }
                else {
                    this.sendDateTime = null;
                    this.isSend = true;
                }
            }
        );
    }


    getActivityTracker(workOrdNo: any) {
        $('.loader').show();
        this.activityService.GetActivityTracker(this.commonService.baseApiUrl, workOrdNo).subscribe(
            data => {
                var datawAct = JSON.stringify(data);
                var actParse = JSON.parse(datawAct);
                var actList = JSON.parse(actParse["_body"]);
                this.workOrdActTrackerList = null;
                if (actList != null) {
                    this.workOrdActTrackerList = actList;
                    console.log(this.workOrdActTrackerList);
                    $('.loader').hide();
                }
            }
        );
    }

    createWorkOrderAssignment() {
        $('.loader').show();
        if (this.workOrdAssPage.valid) {
            let fDate = this.datepipe.transform(this.workOrdAssPage.controls["fromdate"].value, 'yyyy/MM/dd HH:mm:ss');
            let tDate = this.datepipe.transform(this.workOrdAssPage.controls["todate"].value, 'yyyy/MM/dd HH:mm:ss');
            var contact = {
                UserId: this.assUserID,
                WorkOrderId: localStorage.getItem("WorkOrderId"),
                OldWorkOrderId: localStorage.getItem("OldWorkOrderId"),
                WorkOrderNo: localStorage.getItem("WorkOrderNo"),
                Description: this.workOrdAssPage.controls["description"].value,
                RateType: this.workOrdAssPage.controls["ratetype"].value,
                Rate: this.workOrdAssPage.controls["rate"].value,
                FromDate: fDate,
                ToDate: tDate,
                WorkOrderAssignmentStatus: "Created"
            };
            this.workOrderService.CreateWorkOrderAssignment(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    $('#myModal').modal('toggle');
                    if (userInfo == "Ok") {
                        this.getWorkOrderAssignment(localStorage.getItem("WorkOrderId"));
                        this.showSwal("Work Order Assigned");
                    }
                    else if (userInfo == "already") {
                        this.showSwal("Work Order already assigned!!");
                    }
                    this.workOrdAssPage.reset();
                    $('.loader').hide();
                });
        } else {
            this.validateAllFormFields(this.workOrdAssPage);
        }
    }

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }

    valueChanged(newVal) {
        this.assUserID = newVal.ID;
    }

    resetWorkOrderAssignment() {
        this.workOrdAssPage.reset();
    }

    public myListFormatter(data: any): string {
        return data['UserName'];
    }

    getWorkOrdersByWorkOrdID(workOrdID: any) {
        $('.loader').show();
        this.workOrderService.GetWorkOrdersByWorkOrdID(this.commonService.baseApiUrl, workOrdID).subscribe(
            data => {
                var dataWorkOrders = JSON.stringify(data);
                var workParse = JSON.parse(dataWorkOrders);
                var workOrderInfo = JSON.parse(workParse["_body"]);

                localStorage.removeItem("WorkOrderId");
                localStorage.removeItem("OldWorkOrderId");
                localStorage.removeItem("WorkOrderNo");

                if (workOrderInfo != null) {
                    this.workOrderDetail = workOrderInfo;
                    var wAddress = '';

                    if (workOrderInfo.AddressLine1 != null && workOrderInfo.AddressLine1 != '') {
                        wAddress += workOrderInfo.AddressLine1 + ",";
                    }
                    if (workOrderInfo.AddressLine2 != null && workOrderInfo.AddressLine2 != '') {
                        wAddress += workOrderInfo.AddressLine2 + ",";
                    }
                    if (workOrderInfo.AddressLine3 != null && workOrderInfo.AddressLine3 != '') {
                        wAddress += workOrderInfo.AddressLine3 + ",";
                    }
                    if (workOrderInfo.Town != null && workOrderInfo.Town != '') {
                        wAddress += workOrderInfo.Town + ",";
                    }
                    if (workOrderInfo.State != null && workOrderInfo.State != '') {
                        wAddress += workOrderInfo.State + ",";
                    }
                    if (workOrderInfo.PostalCode != null && workOrderInfo.PostalCode != '') {
                        wAddress += workOrderInfo.PostalCode + ",";
                    }
                    if (wAddress != null && wAddress != '') {
                        this.wrokOrdAddress = wAddress.slice(0, -1);
                    }
                    else {
                        this.wrokOrdAddress = "7 Peace St, Harlaxton QLD 4350";
                    }

                    localStorage.setItem("WorkOrderId", workOrderInfo.ID);
                    localStorage.setItem("OldWorkOrderId", workOrderInfo.OldWorkOrderId);
                    localStorage.setItem("WorkOrderNo", workOrderInfo.WorkOrderNo);

                    this.getWorkOrderAssignment(localStorage.getItem("WorkOrderId"));
                    this.getWorkOrderDocuments(localStorage.getItem("WorkOrderId"));

                    $('.loader').hide();
                    this.getLocation(this.wrokOrdAddress); this.getLocation(this.wrokOrdAddress);
                }
            }
        );
    }
    getLocation(address: string) {
        if (address != null) {
            this.http.get("https://maps.google.com/maps/api/geocode/json?address=" + address + "&key=AIzaSyDvC4D3B6FJvtMZLOfUsIUnpUxpET_uc-w").subscribe(
                data => {
                    var dataMap = JSON.stringify(data);
                    var mapParse = JSON.parse(dataMap);
                    var mapList = JSON.parse(mapParse["_body"]);
                    if (mapList != null) {
                        console.log(mapList);
                        this.latitude = mapList["results"][0]["geometry"]["location"]["lat"];
                        this.longitude = mapList["results"][0]["geometry"]["location"]["lng"];
                        this.zoom = 10;
                    }
                }
            );
        }
        else {
            this.latitude = -33.865143
            this.longitude = 151.209900;
            this.zoom = 10;
        }
    }

    addWorkOrderDocument() {
        this.uploader.uploadAll();
    }

    getWorkOrderDocuments(workOrdId: any) {
        $('.loader').show();
        this.workOrderService.GetWorkOrderDocuments(this.commonService.baseApiUrl, workOrdId).subscribe(
            data => {
                var datawAsg = JSON.stringify(data);
                var wAsgParse = JSON.parse(datawAsg);
                var wAsgList = JSON.parse(wAsgParse["_body"]);
                this.workOrderDocsList = null;
                if (wAsgList != null) {
                    this.workOrderDocsList = wAsgList;
                    $('.loader').hide();
                }
            }
        );
    }

    deleteWorkOrderDocuments(workDocsID: any, workOrdId: any, workOrderNo: any, fileName: string) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.workOrderService.DeleteWorkOrderDocuments(this.commonService.baseApiUrl, workDocsID, workOrderNo, fileName).subscribe(
                data => {
                    var datawAsg = JSON.stringify(data);
                    var wAsgParse = JSON.parse(datawAsg);
                    var wAsgList = JSON.parse(wAsgParse["_body"]);
                    if (wAsgList == true) {
                        $('.loader').hide();
                        this.getWorkOrderDocuments(workOrdId);
                        swal({
                            title: 'Deleted!',
                            text: 'Your file has been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                }
            );
        }).catch(swal.noop);
    }

    handleUpload(data): void {
        if (data && data.response) {
            data = JSON.parse(data.response);
            this.uploadedFiles.push(data);
        }
    }
}


