import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { WoAssignmentRoutes } from './wo-assignment.routing';
import { WoAssignmentComponent } from './wo-assignment.component';
import { MaterialModule } from '../app.module';
import { AgmCoreModule } from '@agm/core';
import { NguiAutoCompleteModule } from '@ngui/auto-complete';
import { Ng2AutoCompleteModule } from 'ng2-auto-complete';
import { FieldErrorDisplayModule } from '../field-error-display/field-error-display.module';
import { FileSelectDirective, FileDropDirective, FileUploadModule } from 'ng2-file-upload';
import { Md2DatepickerModule, MdNativeDateModule } from 'md2';


@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(WoAssignmentRoutes),
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,   
        NguiAutoCompleteModule,  
        Ng2AutoCompleteModule,  
        FieldErrorDisplayModule, 
        FileUploadModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyALhBWvI6EuL2Vul0ykYis51D_ckKtxVCc',
            libraries: ["places"]
        }),
        Md2DatepickerModule,
        MdNativeDateModule       
    ],
    declarations: [
        WoAssignmentComponent
    ]
})

export class WoAssignmentModule { }


